import { ApiCRMService } from './api-crm.service';
import { StorageService } from './storage.service';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment.prod';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { BehaviorSubject } from 'rxjs';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  api = environment.apiUrl;
  public userData$ = new BehaviorSubject<any>([]);
  appID: any;
  headerOption = { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) };

  arrRegistrationData = "";
  OTP = "";
  isTimeOut: boolean;

  arrRegData={};

  constructor(private http: HttpClient, private storageService: StorageService, private router: Router) {
    this.getUserData();
   }


  //#region Mobile App Auth Start
  login(body: any) {
    const url = this.api + 'login/Login';
    return this.http.post<any>(url, body.toString(), this.headerOption).toPromise();
  }
  loginWithOtp(body: any) {
    const url = this.api + 'login/LoginWithOtp';
    return this.http.post<any>(url, body.toString(), this.headerOption).toPromise();
  }
  async getAppRegistration() {
    var authToken = "", loginID = "";    
    await this.getLoginID().then(res => { loginID = res });
    const url = this.api + 'login/GetLoginID/' + loginID;    
    return this.http.get<any>(url).toPromise();
  }
  setAppRegistration(body: any) {
    const url = this.api + 'login/AddLogin';
    return this.http.post<any>(url, body.toString(), this.headerOption).toPromise();
  }
  addLoginWithOtp(body: any) {
    const url = this.api + 'login/AddLoginWithOtp';
    return this.http.post<any>(url, body.toString(), this.headerOption).toPromise();
  }
  async putAppRegistration(body: any) {
    var authToken = "";
    const url = this.api + 'login/UpdateLogin';
    await this.getToken().then(res => { authToken = res });
    let headerOption = { headers: new HttpHeaders({ 'Content-Type': 'application/json', 'Authorization': 'bearer ' + authToken }) };
    return this.http.put<any>(url, body.toString(), headerOption).toPromise();
  }
  checkDuplicates(body: any) {
    const url = this.api + 'login/checkduplicate';
    return this.http.post<any>(url, body.toString(), this.headerOption).toPromise();
  }

  logout() {
    this.storageService.removeItem("OAuth").then(res => {
      this.storageService.removeItem("userdata");
      this.storageService.removeItem("appdata");
      this.storageService.removeItem("arrAppUni");
      this.storageService.removeItem("counsellorData");
      this.storageService.removeItem("predictorForm");
      this.storageService.removeItem("selectedPackage");
      this.storageService.removeItem("intakeYear");
      this.userData$.next('');
      this.router.navigate(['/login-new'],{ replaceUrl: true });     
    });
  }

  async changePassword(body: any) {
    var authToken = "";
    const url = this.api + 'login/ChangePassword';
    await this.getToken().then(res => { authToken = res });
    let headerOption = { headers: new HttpHeaders({ 'Content-Type': 'application/json', 'Authorization': 'bearer ' + authToken }) };
    return this.http.put<any>(url, body.toString(), headerOption).toPromise();
  }

  async getLoginID(): Promise<string> {
    var loginID = "";
    // await this.userData$.subscribe(data => {
    //   loginID = data.appLoginID;
    // })
    return await this.storageService.getObject("userdata").then(resp=> loginID = resp["appLoginID"])    
  }
  getPassword(body: any) {
    const url = this.api + 'login/GetPData';
    return this.http.post<any>(url, body.toString(), this.headerOption).toPromise();
  }
  getReferral(body: any) {
    const url = this.api + 'login/GetReferral';
    return this.http.post<any>(url, body.toString(), this.headerOption).toPromise();
  }
  getMyReferrals(body: any) {
    const url = this.api + 'login/GetMyRefferals';
    return this.http.post<any>(url, body.toString(), this.headerOption).toPromise();
  }

  //#endregion



  //== Get User Data
  async getUserData(applicationID?: number) {
    await this.storageService.getObject("userdata").then(res => {
      var arrData = res;
      if (applicationID != undefined) {
        arrData["applicationID"] = applicationID;
        this.storageService.setObject("userdata", arrData);
      }
      this.userData$.next(arrData);
    })
  }

  //== Get Token
  async getToken(): Promise<string> {
    var token = "";
    await this.userData$.subscribe(data => {
      if(data){
        token = data.token;
      }
    });
    // if (token == undefined || token == "" || token == null) {
    //   this.getUserData();
    //   await this.userData$.subscribe(data => {
    //     token = data.token;
    //   })
    // }
    return token;
  }

  // getTokenStorage(): string {
  //   var token = localStorage.getItem("_cap_OAuth");
  //   return token;
  // }

  // isTokenExpired(token?: string): boolean {
  //   if(!token) token = this.getTokenStorage();
  //   if(!token) return true;

  //   const date = helper.getTokenExpirationDate(token);
  //   console.log(date);
  //   if(date === undefined) return false;
  //   return !(date.valueOf() > new Date().valueOf());
  // }

  //== Get Appplication ID
  async getApplicationID(): Promise<string> {
    var appID = "";
    await this.userData$.subscribe(data => {
      appID = data.applicationID;
    });
    if (appID == undefined) {
      this.getUserData();
      await this.userData$.subscribe(data => {
        appID = data.applicationID;
      })
    }
    return appID;
  }
  setRegistration(body: any) {
    const url = this.api + 'application/AddApplication';
    return this.http.post<any>(url, body.toString(), this.headerOption).toPromise();
  }
  
  checkApplicationDuplicates(body: any) {
    const url = this.api + 'application/checkduplicate';
    return this.http.post<any>(url, body.toString(), this.headerOption).toPromise();
  }
}
