import { AlertService } from './../services/alert.service';
import { Router } from '@angular/router';
import { AuthService } from 'src/app/services/auth.service';

import {
  HttpRequest,
  HttpHandler,
  HttpEvent,
  HttpInterceptor,
  HttpResponse,
  HttpErrorResponse
} from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { map, catchError } from 'rxjs/operators';
import { Injectable } from '@angular/core';

@Injectable()
export class Interceptor implements HttpInterceptor {

  counter = 0;
  token = "";
  constructor(private auth: AuthService, private alert: AlertService) {
  }

  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    this.auth.userData$.subscribe((resp: any) => {
      if (resp == null) {
        return false;
      }
      else {
        this.token = resp.token;
      }
    })
    if (this.token) {
      if (request.url.includes('api.moksh16.in')) {
        request = request.clone({
          setHeaders: {
            'Authorization': 'bearer ' + this.token
          }
        });
        if (!request.headers.has('Content-Type') && !request.url.includes('AddDocument')) {
          request = request.clone({
            setHeaders: {
              'content-type': 'application/json'
            }
          });
        }        
      }
    }
    request = request.clone({
      headers: request.headers.set('Accept', 'application/json')
    });
    return next.handle(request).pipe(
      map((event: HttpEvent<any>) => {
        if (event instanceof HttpResponse) {
          // console.log('event--->>>', event);
        }
        return event;
      }),
      catchError((error: HttpErrorResponse) => {
        if (error.status === 401) {
          // if (error.error.success === false) {
          //   console.log('Login failed');
          // } else {
          //   this.router.navigate(['/login']);
          // }
          //this.alert.basciAlert("Opps! Session Expired. Please login again.","","",['OK']);
          this.auth.logout();          
        }
        return throwError(error);
      }));
  }
}