import { Injectable } from '@angular/core';
import { ToastController } from '@ionic/angular'  

@Injectable({
  providedIn: 'root'
})
export class ToastService {

  constructor(public toastController: ToastController) { }

  async presentToast(infoMessage: string) {
    const toast = await this.toastController.create({
      message: infoMessage,
      duration: 2000,
      position: 'top'      
    });
    toast.present();
  }
  async toastError(infoMessage: string) {
    const toast = await this.toastController.create({
      message: infoMessage,
      duration: 3000,
      position: 'top',
      color: 'danger',
      buttons: [
        {
          side: 'start',
          icon: 'alert-circle-outline',                
        }
      ]
    });
    toast.present();
  }
  async toastSuccess(infoMessage: string) {
    const toast = await this.toastController.create({
      message: infoMessage,
      duration: 3000,
      position: 'top',
      color: 'success',      
      buttons: [
        {
          side: 'start',
          icon: 'checkmark-outline',                
        }
      ]
    });
    toast.present();
  }
  async toastWarning(infoMessage: string) {
    const toast = await this.toastController.create({
      message: infoMessage,
      duration: 3000,
      position: 'top',
      color: 'warning',      
      buttons: [
        {
          side: 'start',
          icon: 'warning-outline',                
        }
      ]
    });
    toast.present();
  }
}
