import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment.prod';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { AuthService } from './auth.service';

@Injectable({
  providedIn: 'root'
})
export class ApiProgramsService { 
  api = environment.apiUrl;
  headerOption = { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) };
  ipAddress = "";

  productDepartmentID="";
  universityID = "";
  universityMainID = "";
  applicationYearID = "";

  // countryID = "";
  // degreeID = "";
  // programInteresetID = "";
  // programID = "";
  // courseID = null;
  
  // universityType = "";
  // universityLanguage = "";
  
  // budget:any;
  // establishedYear:any;
  // mokshRating:any;

  universityName = "";

  filteredData = {'countryId' : null, 'degreeId' : null,'courseId':null,'progInterestId' : null, 'programId' : null, 'universityType' : '', 'universityLanguage':'','budget':null,'establishedYear':null,'mokshRating':null}
  filterCount=0;
  //#region 
  arrCountry = []
  //#endregion

  constructor(private http: HttpClient, private auth: AuthService ) { }


  // getCountry(postData: any): Observable<any> {
  //   const url = this.api + 'Program/GetCountry';
  //   return this.http.post<any>(url, postData)
  //     .pipe(
  //       tap(_ => this.log('response received')),
  //       catchError(this.handleError('getDetails', []))
  //     );
  // }

  async getCountry(postData: any) {
    var token = "";
    await this.auth.getToken().then(resp => {token = resp});
    const url = this.api + 'Program/GetCountry';
    const headers = new HttpHeaders({ 'Content-Type': 'application/json', 'Authorization': 'bearer ' + token });
    return this.http.post<any>(url, postData, { headers }).toPromise();
  }
  async getCountryOnProg(postData: any) {
    var token = "";
    await this.auth.getToken().then(resp => {token = resp});
    const url = this.api + 'Program/GetCountryOnProg';
    const headers = new HttpHeaders({ 'Content-Type': 'application/json', 'Authorization': 'bearer ' + token });
    return this.http.post<any>(url, postData, { headers }).toPromise();
  }
  async getDegree(postData: any) {
    var token = "";
    await this.auth.getToken().then(resp => {token = resp});
    const url = this.api + 'Program/GetDegree';
    const headers = new HttpHeaders({ 'Content-Type': 'application/json', 'Authorization': 'bearer ' + token });
    return this.http.post<any>(url, postData, { headers }).toPromise();
  }
  async getInterestedProgram(postData: any) {
    var token = "";
    await this.auth.getToken().then(resp => {token = resp});
    const url = this.api + 'Program/GetProgramInterest';
    const headers = new HttpHeaders({ 'Content-Type': 'application/json', 'Authorization': 'bearer ' + token });
    return this.http.post<any>(url, postData, { headers }).toPromise();
  }

  async getProgramSpecialization(postData: any) {
    var token = "";
    await this.auth.getToken().then(resp => {token = resp});
    const url = this.api + 'Program/GetProgram';
    const headers = new HttpHeaders({ 'Content-Type': 'application/json', 'Authorization': 'bearer ' + token });
    return this.http.post<any>(url, postData, { headers }).toPromise();
  }

  async getUniversities(postData: any) {
    var token = "";
    await this.auth.getToken().then(resp => {token = resp});
    const url = this.api + 'program/getUniversitylist';
    const headers = new HttpHeaders({ 'Content-Type': 'application/json', 'Authorization': 'bearer ' + token });
    return this.http.post<any>(url, postData, { headers }).toPromise();
  }
  async getUniversityDetails(universityMianId) {
    var token = "";
    await this.auth.getToken().then(resp => {token = resp});
    const url = this.api + 'program/GetUniversityID/'+universityMianId;
    const headers = new HttpHeaders({ 'Content-Type': 'application/json', 'Authorization': 'bearer ' + token });
    return this.http.get<any>(url,  { headers }).toPromise();
  }  
  async getGallery(postData: any) {
    var token = "";
    await this.auth.getToken().then(resp => {token = resp});
    const url = this.api + 'Program/GetUniversityGallery';
    const headers = new HttpHeaders({ 'Content-Type': 'application/json', 'Authorization': 'bearer ' + token });
    return this.http.post<any>(url, postData, { headers }).toPromise();
  }
  async GetUniversityFees (postData: any) {
    var token = "";
    await this.auth.getToken().then(resp => {token = resp});
    const url = this.api + 'Program/GetUniversityFees ';
    const headers = new HttpHeaders({ 'Content-Type': 'application/json', 'Authorization': 'bearer ' + token });
    return this.http.post<any>(url, postData, { headers }).toPromise();
  }


  resetUniversityFilter(arrSelection){
    if (arrSelection != "") {
      this.filteredData.countryId = arrSelection["countryId"];
      this.filteredData.progInterestId = arrSelection["progInterestId"];
    }
    this.filteredData.universityType="";
    this.filteredData.universityLanguage="";
    this.filteredData.budget=null;
    this.filteredData.establishedYear=null;
    this.filteredData.mokshRating=null; 
    this.filterCount = 0;   
  }

}
